
import mixin from '../libs/mixin'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
  name: 'my',
  components: {
    Group,
    Cell
  },
  mixins: [mixin],
  data () {
    return {
    	sex:2,
    	title1:'今天是5月21号，星期日，so bad',
    	title2:'明天要上班，(；′⌒`)',
    	title3:'我喜欢吃西瓜',
    	title4:'还有...鱼'
    }
  },
  mounted: function(){
  	this.setDocumentTitle("my")
  }
}



